import React from 'react'
import {Button, Drawer, Toolbar} from 'devextreme-react'

import NavigationList from './NavigationList.js'

class App extends React.Component {

  state = {
    opened: true,
    openedMenu: false
  }

  toolbarItemsOpened = [
    {
      location: 'before',
      locateInMenu: 'never',
      template: () => {
        return '<div class=\'logo\'><img src="./logo.svg" alt="" style="margin-left: 10px"></div>'
      }
    },
    {
      location: 'center',

      locateInMenu: 'never',
      template: () => {
        return '<div class=\'toolbar-label\' style="font-size: 1.5em"><b>Счетчики</b></div>'
      }
    },
    {
      location: 'after',

      template: () => {
        return '<img src="./burger.svg" >'
      },
      onClick: () => this.setState({opened: !this.state.opened})
    }
  ]
  toolbarItemsCollapsed = [
    {
      locateInMenu: 'never',
      template: () => {
        return '<div class=\'logo_includes_menu\'><img src="./logoMin.svg" alt="" style="margin-left: 7px"></div>'
      },
    }
    , {
      template: () => {
        return '<img class=\'burger\' src="./burgerMin.svg">'
      },
      onClick: () => this.setState({opened: !this.state.opened})
    }
  ]


  render() {
    const {opened, openedMenu, openedStateMode, position, revealMode} = this.state

    return (

      <React.Fragment>
        <Toolbar
          width={opened ? 350 : 39}
          height={60}
          className={'opened_toolbar'}
          visible={opened ? true : false}
          items={opened ? this.toolbarItemsOpened : this.toolbarItemsCollapsed}
        />
        <Toolbar
          width={opened ? 350 : 39}
          visible={opened ? false : true}
          className={'closed_toolbar'}
          items={opened ? this.toolbarItemsOpened : this.toolbarItemsCollapsed}
        />
        <Drawer
          opened={opened}
          openedStateMode={openedStateMode}
          position={position}
          revealMode={revealMode}
          closeOnOutsideClick={true}
          height={800}
          render={() => <NavigationList opened={opened}
                                        toggleAside={() => this.setState({opened: !this.state.opened})}/>}
        >
        </Drawer>
        <div className={`profile_container${openedMenu ? '_fixed' : ''}`}>
          <div>
            <Button
              width={240}
              icon={'card'}
              type={'success'}
              onClick={() => this.setState({openedMenu: !this.state.openedMenu})}
            />
            <Button
              width={240}
              icon={'user'}
              type={'normal'}

              stylingMode={'outlined'}
              text={'Настройик профиля'}

              onClick={() => console.log('set profile')}
            />
            <Button
              width={240}
              type={'normal'}

              stylingMode={'outlined'}
              icon={'preferences'}
              text={'Настройик системы'}

              onClick={() => console.log('set system')}
            />
            <Button
              width={240}
              type={'danger'}
              icon={'runner'}
              stylingMode={'outlined'}
              text={'Выход'}

              onClick={() => console.log('exit')}
            />
          </div>
        </div>
      </React.Fragment>
    )
  }
}

export default App
